.VideoFooter{
    position: relative;
    bottom: 20%;
    color: aliceblue;
    margin-left: 30px;
}

.VideoFooter__text h3{
padding-bottom:  20px;
}

.VideoFooter__text p{
    padding-bottom:  20px;
}

.VideoFooter__Music{
    display: flex;
}


.VideoFooterMusic__text{
    
    width: 70%;
    overflow-x: hidden;
    
}
.VideoFooterMusic__text p{
    animation: moveTheText 3s infinite linear;
}


.VideoFooter__Record{
    animation: spinTheRecord 3s infinite linear;
    position: absolute;
    bottom: 5px;
    right: 20px;
    height: 50px;
    filter: invert(1);
}

@keyframes spinTheRecord {
    0%{        
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
    
}

@keyframes moveTheText {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(-100%);
    }
    
}

@media only screen and (max-width: 440px){
    .VideoFooter{
        bottom: 22%;
    }
    .VideoFooterMusic__text{
        width: 78%;
    }
  
  }